@media screen and (max-width: 425px) {
  .popup__programm-name {
    padding: 0 5px;
  }
}

.popup__programm-name .m-field, .popup__programm-name .m-field-angle {
  min-width: 0;
  max-width: 100%;
  width: 100%;
}

.popup .nice-select {
  max-width: 280px;
  width: 100%;
}

.popup .nice-select .current {
  color: #a2a1a1;
  opacity: 1;
  font-family: 'ral';
  font-weight: bold;
}

.popup-content {
  position: relative;
  background-color: #edf2f6;
  border-radius: 4px;
  margin: 100px 0;
}

.popup.visible {
  visibility: visible;
  opacity: 1;
  perspective: 0.3;
  transform: scale(1);
}

.popup__block {
  padding: 30px 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.popup__block.white {
  background-color: #fff;
}

.popup__close {
  position: absolute;
  top: -33px;
  right: -33px;
  cursor: pointer;
  transition: 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 768px) {
  .popup__close {
    right: 10px;
  }
}

.popup__close:hover {
  transform: rotate(90deg);
}

.popup .m-button {
  max-width: 275px;
  height: 55px;
  margin: 10px 0;
}

.popup__delete-button {
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup__top-title {
  font-size: 20px;
  font-weight: "ral";
  color: #1f2942;
  font-weight: bold;
  margin-bottom: 8px;
}

.popup__top-desc {
  color: #96a7b5;
  font-size: 12px;
  font-family: "ral";
}

.popup__desc {
  color: #434343;
  font-weight: 500;
  font-family: "ral";
  margin-bottom: 10px;
}

.popup__desc span {
  color: #000;
  line-height: 2;
  font-weight: bold;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  cursor: pointer;
  z-index: 90;
  width: 100vw;
  height: 100vh;
  background-color: #000000;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  transform-origin: 0 0;
}

.overlay.visible {
  visibility: visible;
  opacity: 0.5;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.add-programm .popup__container {
  max-width: 610px;
}

.popup-add-doc .popup__top {
  padding-bottom: 10px;
}
